.cart-header{
    padding-top: 0.5rem;
}

.orders{
    margin-top: 2rem;
    min-height: calc(100vh-15rem);
}

.orders-heading, .orders-total{
    width: 100%;
    max-width: 600px;
}

.orders-heading{
    border-bottom: 2px solid #000;
    margin-bottom: 5.5rem;
}

.orders-total{
    border-top: 2px solid #000;
    text-align: right;
    margin-left: auto;
    margin-top: 3rem;
}