header{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100vh;
    padding-top: 0.5rem;

}

.header-img{
    width: 100%;
    align-self: center;
}

.header-content{
    display: flex;
    flex-direction: column;
}

.content-main{
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
}

.content-main h1{
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

.content-main button{
    background-color: black;
    color: white;
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    outline: none;
    border: none;
    font-size: 1rem;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    margin-top: 2.5rem;
    transition: 0.2s ease-out;
}
.btn:hover{
    background-color: #c2cad0;
    color: black;
    cursor: pointer;
    font-weight: bold;
    transition: 0.2s ease-out;
}

.content-main p{
    font-weight: 500;
}

@media(max-width: 800px){
    .content-main h1{
      font-size: 1.2rem;
      margin-bottom: 1rem;
    }
    .content-main p{
      font-size: 0.8rem;
    }
    .content-main button {
      font-size: 0.8rem;
      width: 150px;
      padding: 0.5rem, 1rem;
      margin-top: 1.5rem;
    }
  }